		// 微信小程序登录
		weChatLogin() {
			let _this = this;
			this.$refs.popup.close()
			uni.getUserInfo({
				provider: 'weixin',
				success: function(infoRes) {
					uni.login({
						success: function(res) {
							uni.showToast({
								title: "登录成功"
							})
							_this.$store.state.openId = res.code
							_this.$store.state.userInfo = infoRes.userInfo
							uni.setStorageSync("openId", res.code)
							uni.setStorageSync("userInfo", infoRes.userInfo)
							// 获取code  
							uni.setStorageSync('isCanUse', false); //记录是否第一次授权  false:表示不是第一次授权
							// console.log(res.code, infoRes.userInfo)
							// _this.weixinLogin(res.code, infoRes.userInfo);
						}
					});


				},
				fail(res) {}
			});
		},


































		// 跳转
		jump(index) {
			if (index === 3) {
				uni.navigateTo({
					url: "/pages/collect/collect"
				})
			} else if (index === 4) {
				uni.navigateTo({
					url: "/pages/lookHistory/lookHistory"
				})
			} else if (index === 6) {
				uni.navigateTo({
					url: "/pages/addressList/addressList"
				})
			} else if (index === 10) {
				uni.navigateTo({
					url: "/pages/service/service"
				})
			}else if(index === 11){
				if(this.$store.state.userInfo){
					this.$store.state.userInfo = null
					this.$store.state.openId = null
					uni.removeStorageSync("openId")
					uni.removeStorageSync("userInfo")
					uni.showToast({
						title:"退出成功",
						icon:"none"
					})
				}else{
					uni.showToast({
						title:"暂未登录，请登录",
						icon:"none"
					})
				}
				
			}
		}
	},

